.App {
  text-align: center;
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background: lightblue;
  color: white;
}

.nav-links {
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

button {
  background-color: rgb(80, 83, 255);
  border-style: none;
  border-radius: 20%;
}

button:hover {
  background-color: aquamarine;
}
